.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.footer{
  display: flex;
  justify-content: space-between;
}

.footer p{
  color: #494949;
  font-family: Lato;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
}

.loginContainer, .signupContainer{
  background-color: #FFFBED;
  min-height: 100vh;
  padding-top: 50px;
  font-style: 'lato';
  font-size: 16px;
  font-weight: 700px;
  padding-left: 35px;
  padding-right: 35px;
  padding-bottom: 50px;
}
